
import { defineComponent } from 'vue'

export default defineComponent({
    props: {
        patient: {
            type: Object,
            required: true
        },
        isActive: {
            type: Boolean
        }
    },
    computed: {
        stateClass(): any {
            return {
                'active-card-color': this.isActive,
                'inactive-card-color': !this.isActive
            }
        }
    }
})
